import { Component, AfterViewInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { UsersService } from '../../../shared/service/users.service';
import { System1Service } from 'src/app/shared/service/admin-stats/system1.service';

@Component({
  selector: 'app-system1',
  templateUrl: './system1.component.html',
  styleUrls: ['./system1.component.scss']
})
export class System1Component implements AfterViewInit {
  range = {
    startDate: '',
    endDate: '',
  };
  rows: any[];
  allStats: any[];
  loadingIndicator = true;
  selectedCompany: any;
  groupFlag = false;
  @ViewChild('expandableTable') table: any;
  constructor(
    private cdr: ChangeDetectorRef,
    private userService: UsersService,
    private system1Service: System1Service,
  ) {
    this.selectedCompany = this.getSelectedCompanyFromLocalStorage();
   }

  async ngAfterViewInit() {
    this.rows = [];
    this.rows = await this.getAllSystem1Stats(
      this.selectedCompany,
      this.range.startDate,
      this.range.endDate
    );
  }
  public async updateReportingFiltering(range) {
    // console.log('Update report filtering....');
    this.range = range;
    this.rows = await this.getAllSystem1Stats(
      this.selectedCompany,
      this.range.startDate,
      this.range.endDate
    );
    this.groupFlag = false;
    this.refreshTable();
  }

  //get Stat all
  getAllSystem1Stats(company, startDate, endDate) {
    return this.system1Service.getAllSystem1Stats(company, startDate, endDate).toPromise().then((response) => {
      console.log('getAllSystem1Stats() response:', response);
      this.loadingIndicator = false;
      this.allStats = response.stats;
      return this.allStats;
    });
  }

  getSummaryMetrics(company) {
    return [];
  }
  toggleExpandRow(row) {
    console.log('Toggled Expand Row!', row);
    this.table.rowDetail.toggleExpandRow(row);
  }

  onDetailToggle(event) {
    console.log('Detail Toggled', event);
  }
  refreshTable() {
    this.cdr.markForCheck();
  }
  //Gets the Selected Company from Local Storage
  getSelectedCompanyFromLocalStorage() {
    return this.userService.getSelectedCompanyFromLocalStorage();
  }
}
